import React from 'react';
import { useNavigate } from 'react-router-dom';

interface BarberCardProps {
    barberId: string;
    name: string;
    specialization: string;
    // image: string; // Barber's image or avatar
}

const BarberCard: React.FC<BarberCardProps> = ({ barberId, name, specialization }) => {
    const navigate = useNavigate();

    const handleCardClick = () => {
        // Navigate to the barber's detailed page when the card is clicked
        navigate(`/barber/${barberId}`);
    };

    return (
        <div className="barber-card" onClick={handleCardClick}>
            {/* Barber image */}
            {/*<img src={image} alt={name} className="barber-image" />*/}
            <h3>{name}</h3>
            <p>{specialization}</p>
        </div>
    );
};

export default BarberCard;
