import React, { useState } from 'react';
import '../styles/LandingPage.css';
import BarberCard from './BarberCard';

const LandingPage: React.FC = () => {
    const [scissorsRotate, setScissorsRotate] = useState(false);
    const [searchQuery, setSearchQuery] = useState('');

    const handleScissorsHover = () => {
        setScissorsRotate(!scissorsRotate);
    };

    // Mock data for barbers
    const barbers = [
        { id: '1', name: 'John Doe', specialization: 'Haircuts' },
        { id: '2', name: 'Jane Smith', specialization: 'Shaves' },
        { id: '3', name: 'Alex Black', specialization: 'Beard Grooming' },
        { id: '4', name: 'Sara White', specialization: 'Haircuts' },
        { id: '5', name: 'Chris Green', specialization: 'Shaves' },
        { id: '6', name: 'Michael Brown', specialization: 'Haircuts' },
        // { id: '7', name: 'Emily Johnson', specialization: 'Beard Grooming' },
        // { id: '8', name: 'David Miller', specialization: 'Shaves' },
        // { id: '9', name: 'Olivia Davis', specialization: 'Haircuts' },
        // { id: '10', name: 'James Wilson', specialization: 'Beard Grooming' },
        // { id: '11', name: 'Sophia Martinez', specialization: 'Haircuts' },
        // { id: '12', name: 'Liam Garcia', specialization: 'Shaves' },
        // { id: '13', name: 'Ethan Lee', specialization: 'Beard Grooming' },
        // { id: '14', name: 'Isabella Perez', specialization: 'Haircuts' },
        // { id: '15', name: 'Charlotte Robinson', specialization: 'Shaves' },
        // { id: '16', name: 'Benjamin Clark', specialization: 'Beard Grooming' },
        // { id: '17', name: 'Amelia Lewis', specialization: 'Haircuts' },
        // { id: '18', name: 'Henry Walker', specialization: 'Shaves' },
        // { id: '19', name: 'Ava Hall', specialization: 'Beard Grooming' },
        // { id: '20', name: 'Mason Young', specialization: 'Haircuts' },
    ];


    // Handle real-time search
    const handleSearchChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setSearchQuery(event.target.value.toLowerCase());
    };

    // Filter barbers based on search query
    const filteredBarbers = barbers.filter(
        (barber) =>
            barber.name.toLowerCase().includes(searchQuery) ||
            barber.specialization.toLowerCase().includes(searchQuery)
    );

    return (
        <div className="landing-page">
            <h1 className="main-heading">Book Your Barber Appointment</h1>
            <div
                className={`scissors-icon ${scissorsRotate ? 'rotate' : ''}`}
                onMouseEnter={handleScissorsHover}
                onMouseLeave={handleScissorsHover}
            >
                ✂️
            </div>

            {/* Search Input */}
            <input
                type="text"
                placeholder="Search Barbers"
                value={searchQuery}
                onChange={handleSearchChange}
                className="search-input"
            />

            {/* Barber Cards */}
            <div className="barber-cards-container">
                {filteredBarbers.length > 0 ? (
                    filteredBarbers.map((barber) => (
                        <BarberCard
                            key={barber.id}
                            barberId={barber.id}
                            name={barber.name}
                            specialization={barber.specialization}
                        />
                    ))
                ) : (
                    <p>No barbers found</p>
                )}
            </div>
        </div>
    );
};

export default LandingPage;
