// src/components/BarberPage.tsx
import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import 'react-calendar/dist/Calendar.css';
import '../styles/BarberPage.css'
import BasicDateCalendar from "./common/Calendar";

interface Slot {
    time: string;
    available: boolean;
}

const BarberPage: React.FC = () => {
    const { barberId } = useParams<{ barberId: string }>();
    const [selectedDate, setSelectedDate] = useState<Date>(new Date());
    const [selectedSlot, setSelectedSlot] = useState<string>('');

    const handleDateChange = (date: Date) => {
        setSelectedDate(date);
    };

    // Mock data for available slots for each barber
    const slots: Slot[] = [
        { time: '10:00 AM', available: true },
        { time: '11:00 AM', available: false },
        { time: '12:00 PM', available: true },
        { time: '01:00 PM', available: true },
        { time: '02:00 PM', available: false },
    ];

    const handleSlotSelect = (time: string) => {
        setSelectedSlot(time);
    };

    const handleBookAppointment = () => {
        alert(`Your appointment with Barber ${barberId} on ${selectedDate.toLocaleDateString()} at ${selectedSlot} is confirmed!`);
    };

    return (
        <div className="barber-page">
            <h2>Barber {barberId} - Available Slots</h2>

            <h3>Select a Date</h3>
            <BasicDateCalendar />

            {/* Slot Selection */}
            <div className="slot-list">
                {slots.map((slot) => (
                    <div
                        key={slot.time}
                        className={`slot ${slot.available ? 'available' : 'unavailable'}`}
                        onClick={() => slot.available && handleSlotSelect(slot.time)}
                    >
                        {slot.time} - {slot.available ? 'Available' : 'Unavailable'}
                    </div>
                ))}
            </div>

            {selectedSlot && (
                <button onClick={handleBookAppointment} className="book-appointment">
                    Book Appointment on {selectedDate.toLocaleDateString()} at {selectedSlot}
                </button>
            )}
        </div>
    );
};

export default BarberPage;